import React from "react";
import { Link } from "react-router-dom";
import Carousel from "react-bootstrap/Carousel";
import "bootstrap/dist/css/bootstrap.min.css";

const Home = ({ figures, decor }) => {
  return (
    <div className="bg-dark text-light p-4">
      <Carousel className="mb-5">
        {figures.slice(0, 3).map((item, idx) => (
          <Carousel.Item key={idx}>
            <img
              className="d-block w-100"
              src={item.img}
              alt={item.nome}
              style={{ height: "400px", objectFit: "contain", objectPosition: "center" }}
            />
            <Carousel.Caption>
              <h5>{item.nome}</h5>
              <p>{item.tamanho}</p>
            </Carousel.Caption>
          </Carousel.Item>
        ))}
      </Carousel>

      <div className="d-flex justify-content-center gap-5 flex-wrap">
        <Link to="/figures" className="text-decoration-none text-light">
          <div className="card bg-secondary" style={{ width: "18rem", height: "100%" }}>
            <img 
              src={figures[3].img} 
              className="card-img-top"
              alt="Action Figures" 
              style={{ height: "250px", objectFit: "contain", objectPosition: "center" }}
            />
            <div className="card-body text-center">
              <h5 className="card-title">Ver Action Figures</h5>
            </div>
          </div>
        </Link>

        <Link to="/decoracao" className="text-decoration-none text-light">
          <div className="card bg-secondary" style={{ width: "18rem", height: "100%" }}>
            <img 
              src={decor[2].img} 
              className="card-img-top" 
              alt="Decoração" 
              style={{ height: "250px", objectFit: "contain", objectPosition: "center" }}
            />
            <div className="card-body text-center">
              <h5 className="card-title">Ver Decoração</h5>
            </div>
          </div>
        </Link>
      </div>
    </div>
  );
};

export default Home;
